import config from "../config"
import axios from 'axios';
import jwt from 'jsonwebtoken'
import router from '../Routes'

export default {
  namespaced: true,
  state: {
    isFetching: false,
    errorMessage: ''
  },
  mutations: {
    LOGIN_FAILURE(state, payload) {
      state.isFetching = false;
      state.errorMessage = payload;
    },
    LOGIN_SUCCESS(state) {
      state.isFetching = false;
      state.errorMessage = '';
    },
    LOGIN_REQUEST(state) {
      state.isFetching = true;
    }
  },
  actions: {
    loginUser({dispatch}, creds) {
      if (!config.isBackend) {
        dispatch('receiveToken', 'token');
      }
      else {
        dispatch('requestLogin');
        if (creds.social) {
          window.location.href = config.baseURLApi + "/auth/signin/" + creds.social + '?app=' + config.redirectUrl;
        }
        else
        if (creds.email.length > 0 && creds.password.length > 0) {
          axios.post("/auth/signin/local", creds).then(res => {
            const token = res.data;
            dispatch('receiveToken', token);
          }).catch(err => {
            dispatch('loginError', err.response.data);
          })
        }
        else
        {
          dispatch('loginError', 'Something was wrong. Try again');
        }
      }
    },
    receiveToken({dispatch}, token) {
      let user = {};
      // We check if app runs with backend mode
      if (config.isBackend) {
        user = jwt.decode(token).user;
        delete user.id;
      } else {
        user = {
          email: config.auth.email
        }
      }

      localStorage.setItem('token', token);
      localStorage.setItem('user', JSON.stringify(user));
      axios.defaults.headers.common['Authorization'] = "Bearer " + token;
      dispatch('receiveLogin');
    },
    logoutUser() {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      document.cookie = 'token=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
      axios.defaults.headers.common['Authorization'] = "";
      router.push('/login');
    },
    loginError({commit}, payload) {
      commit('LOGIN_FAILURE', payload);
    },
    receiveLogin({commit}) {
      commit('LOGIN_SUCCESS');
      router.push('/user/profile');
    },
    requestLogin({commit}) {
      commit('LOGIN_REQUEST');
    },
  }
}
